import DecryptedText from "./DecryptedText";

export default function HeroContent() {
    return (
        <div className="flex flex-col items-center justify-center text-center pt-0 pb-24">
            <span className="text-emerald-500 uppercase tracking-[0.4em] text-[10px] font-bold mb-6">
                Innovative Design
            </span>
            <h1 className="text-7xl md:text-[9rem] font-bold text-white tracking-tighter leading-none mb-10 flex flex-wrap justify-center gap-x-6">
                <DecryptedText
                    text="Visual"
                    animateOn="view"
                    revealDirection="center"
                    sequential={true}
                    speed={30}
                />
                <DecryptedText
                    text="Impact"
                    animateOn="view"
                    revealDirection="center"
                    sequential={true}
                    speed={30}
                    className="text-emerald-500 italic font-black"
                />
            </h1>
            <p className="text-zinc-500 max-w-lg mx-auto">
                Scroll down to see our curated portfolio reveal in high resolution.
            </p>
        </div>
    );
}